.popup input[type="checkbox"] {
  order: 1;
  bottom: 61px;
  left: 48px; }

.popup input[type="checkbox"] + label {
  position: relative;
  order: 1;
  margin-right: 32px;
  padding-left: 48px;
  font-size: 14px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer; }
  .popup input[type="checkbox"] + label::before {
    position: absolute;
    content: "";
    top: 2px;
    left: 0;
    width: 16px;
    height: 16px;
    border: 1px solid #ffffff; }
  .popup input[type="checkbox"] + label:hover {
